body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.font-roboto {
  font-family: "Roboto Mono", monospace;
}

.font-ubuntu {
  font-family: "Ubuntu", sans-serif;
}

.font-signika {
  font-family: "Signika Negative", sans-serif;
}

.font-noto {
  font-family: "Noto Serif KR", serif;
}

.font-hahmlet {
  font-family: "Hahmlet", serif;
}

.material-symbols-outlined {
  font-variation-settings: "FILL" 0, "wght" 300, "GRAD" 0, "opsz" 48;
}

a {
  text-decoration: none;

  color: inherit; /* blue colors for links too */
  text-decoration: inherit; /* no underline */
}

button {
  border: transparent;
  border-radius: 25px;
  background: linear-gradient(120deg, #59bb8d, #589cb1);
  transition: 0.3s;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}

button:hover {
  transform: scale(1.08) perspective(1px);
}

::-webkit-scrollbar {
  width: 12px;
  background-color: transparent;
}
::-webkit-scrollbar-thumb {
  background-color: #52c7a09f;
  border-radius: 15px;
  transition: 0.3s;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #52c7a0;
}

::-webkit-scrollbar-track {
  background-color: transparent;
}

.page {
  width: calc(100% - 15px);
  padding: 25px;
}

.full-screen {
  position: fixed;
  width: 100vw;
  height: 100vh;
  background-color: #292929;
}

.flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.menu-box {
  background-color: transparent;
  border: 1px solid transparent;
}

.menu-box:hover {
  background-color: #464646;
}

h1 {
  font-family: "Signika Negative", "Noto Serif KR", sans-serif;
  font-size: 64px;
  margin: 0px;
  padding: 0px;
  color: #52c7a0;
}

h2 {
  margin: 0px;
  padding-left: 7px;
  font-size: 36px;
  color: #a1a1a1;
  font-family: "Signika Negative", "Hahmlet", sans-serif;
  margin-top: 25px;
  border-left: 6px solid #52c7a0;
}

h3 {
  margin: 0px;
  padding: 0px;
  color: #a1a1a1;
  margin-top: 15px;
  font-size: 24px;
  font-family: "Signika Negative", "Hahmlet", sans-serif;
}

p {
  margin: 0px;
  color: white;
  font-family: "Hahmlet", sans-serif;
  margin-bottom: 30px;
}
